import React from 'react'
import { useDispatch } from 'react-redux'
import { Link } from 'react-router-dom'
import { addCountPizza, delCountPizza, delPizza, delAllBasket } from '../../redux/reducers/pizzasReducer'
import Button from '../Button/Button'
import ButtonCounter from '../ButtonCounter/ButtonCounter'
import ButtonDel from '../ButtonDel/ButtonDel'
import './Basket.scss'
import trash from '../../assets/img/trash.png'
import trashShop from '../../assets/img/basketShop.png'

function Basket(props) {
    const dispatch = useDispatch()

    const addCount = (e) => {
        dispatch(addCountPizza(e))
        
    }
    const delCount = (e) => {
        dispatch(delCountPizza(e))
    }
    
    const pizzaDel = (e) => {
        if(window.confirm('–í—ã —Ö–æ—Ç–∏—Ç–µ —É–¥–∞–ª–∏—Ç—å –ø–∏—Ü—Ü—É?')){
        dispatch(delPizza(e))
    }
    }

    const allDel = () => {
        if(window.confirm('–í—ã –¥–µ–π—Å—Ç–≤–∏—Ç–µ–ª—å–Ω–æ —Ö–æ—Ç–∏—Ç–µ –æ—Ç—á–∏—Å—Ç–∏—Ç—å –∫–æ—Ä–∑–∏–Ω—É?')){
        dispatch(delAllBasket())
        }
    }

    const pay = (e) => {
       console.log(e)
       alert('–≤–∞—à –∑–∞–∫–∞–∑ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω')
    }
    
    return (
        <>
        {props.total.totalPizzasCount === 0 ?
            <div className="basketHomeContainer">
                <div className="basketHome">
                    <div>
                        <img  src={trashShop} alt="–∫–∞—Ä—Ç–∏–Ω–∫–∞"/>
                    </div>
                </div>
                <div className="titleEmptyBasket">
                    <h1>–í–∞—à–∞ –∫–æ—Ä–∑–∏–Ω–∞ –ø—É—Å—Ç–∞!</h1>
                </div>
                <div className="footerMob">
                    <div>
                        <Link to="/"><Button>–í–µ—Ä–Ω—É—Ç—å—Å—è –Ω–∞–∑–∞–¥</Button></Link>
                    </div> 
                </div>
            </div>
            
        : props.total.totalPizzasCount > 0 &&
        <div>
            <div className="trashbox">
                <div>
                    <img src={trash} onClick={allDel} alt="–∫–∞—Ä—Ç–∏–Ω–∫–∞"/><div>–æ—Ç—á–∏—Å—Ç–∏—Ç—å –∫–æ—Ä–∑–æ–Ω—É</div>
                </div>
                </div>
                <img src="" alt=""/>
            <div className="container_basket">
                {props.pizzas.map((item, index) =>

                <div key={index} className="block_order">
                    <div>
                        <img src={item.imageUrl} alt="–∫–∞—Ä—Ç–∏–Ω–∫–∞"/>
                    </div>

                    <div className="description">
                        <div>
                            <h3>{item.namePizza}</h3>
                        </div>
                        <div >
                            {item.description}
                        </div>
                        <div style={{margin: "10px 0px"}}>
                            
                        <b> —Ç–µ—Å—Ç–æ  {item.type === 0 ? '—Ç–æ–Ω–∫–æ–µ' : '—Ç—Ä–∞–¥–∏—Ü–∏–æ–Ω–Ω–æ–µ'}</b>
                          
                        </div>
                        <div>
                           <b>{item.size} —Å–º</b>
                        </div>
                    </div>

                    <div className="counter">
                        <ButtonCounter click={() => delCount(item)}>-</ButtonCounter>
                        <h4><b>{item.count}</b></h4>
                        <ButtonCounter click={() => addCount(item)}>+</ButtonCounter>
                    </div>
                    
                    <div className="total">
                           <b> –ò—Ç–æ–≥–æ {item.price} ‚ÇΩ </b>
                    </div>
                    <div className="delItem">
                        <ButtonDel click={() => pizzaDel(item)}>x</ButtonDel>
                    </div>
                </div>
                )}
                <div className="footer">
                    <div>
                        <Link to="/"><Button>–í–µ—Ä–Ω—É—Ç—å—Å—è –Ω–∞–∑–∞–¥</Button></Link>
                    </div> 
                    <div className="alltotalprice">
                        <h2>–ò—Ç–æ–≥–æ: {props.total.totalPizzas} ‚ÇΩ</h2>
                    </div> 
                    <div>
                    <Button click={()=>pay(props.pizzas)}>–û–ø–ª–∞—Ç–∏—Ç—å</Button>
                    </div>
                </div>
            </div>
        </div>
}
        </>
    )
}

export default Basket
